<style>
  .i-barrage {
    width: 300px;
    height: 300px;
  }
</style>
<script>
  import iBarrage from 'i-ui/lib/iBarrage'
  export default {
    name: 'i-barrage',
    components: {
      iBarrage
    },
    data () {
      return {
        list: [{
          txt: '~我卡咔咔',
          fontSize: '11px',
          color: '#000',
          speed: 1,
          time: 1
        }, {
          txt: '你踩踩~',
          fontSize: '11px',
          color: '#000',
          speed: 1,
          time: 2
        }, {
          txt: '我是弹幕~',
          fontSize: '11px',
          color: '#000',
          speed: 2,
          time: Infinity
        }],
        runing: true,
        direction: 'left'
      }
    },
    methods: {
      toggleRun () {
        this.runing = !this.runing
      },
      toggleDirection () {
        this.direction = this.direction === 'left' ? 'right' : 'left'
      },
      randomTxt () {
        return eval(`'\\u${(Math.round(Math.random() * 20901) + 19968).toString(16)}'`)  //eslint-disable-line
      },
      doneBarrage (item, idx) {
        console.log('done')
        this.list.splice(idx, 1)
        this.list.push({
          txt: `${this.randomTxt()}${this.randomTxt()}${this.randomTxt()}${this.randomTxt()}`,
          fontSize: '11px',
          color: '#000',
          speed: (Math.random() + 1),
          time: 1
        })
      }
    }
  }
</script>


