<script>
  import iRollBar from 'i-ui/lib/iRollBar'
  export default {
    components: {
      iRollBar
    },
    name: 'i-roll-bar',
    data() {
      return {
        items: []
      }
    },
    created() {
      for (var idx = 0; idx <= 35; idx++) {
        this.items.push(idx)
      }
    },
    methods: {
      roll(done) {
        console.log(Date.now())
        for (var idx = 0; idx <= 10; idx++) {
          this.items.push(idx)
        }
        done()
      }
    }
  }
</script>


